import React, { useState } from 'react';

const tasks = [
  { id: 1, title: 'Nakarmi niewidzialnego kota', completed: true },
  { id: 2, title: 'Policzy wszystkie gwiazdy na niebie ', completed: false },
  { id: 3, title: 'Zapyta lodwk, co dzi na obiad', completed: true },
  { id: 4, title: 'Przesun cian o centymetr w lewo (bo tak)', completed: false },
  { id: 5, title: 'Zgasi wiato w pokoju, w ktrym nikogo nie ma (profilaktycznie)', completed: true },
];

function TaskList() {
  const [filter, setFilter] = useState('all'); 

  const filteredTasks = tasks.filter(task => {
    if (filter === 'all') return true;
    return filter === 'completed' ? task.completed : !task.completed; 
  });

  return (
    <div>
      <h2>Lista zada</h2>
      {/* Wybr filtra */}
      <select onChange={e => setFilter(e.target.value)}>
        <option value="all">Wszystkie</option>
        <option value="completed">Ukoczone</option>
        <option value="incomplete">Nieukoczone</option>
      </select>

      {/* Wywietlanie listy zada */}
      <ul>
        {filteredTasks.map(task => (
          <li key={task.id}>{task.title}</li>
        ))}
      </ul>
    </div>
  );
}

export default TaskList;
