import './App.css';
import React, { useState } from 'react';
import ToggleDetails from './ToggleDetails';
import ScoreDisplay from './ScoreDisplay';
import TaskList from './TaskList'; 
import UserList from './UserList';
import TimerCounter from './TimerCounter';


function App() {
  const [userScore, setUserScore] = useState('');

  const handleChange = (e) => {
    const value = e.target.value;
    if (/^\d*$/.test(value)) { 
      setUserScore(value);
    }
  };

  return (
    <div className="App">
      <h1>wiczenie 1  Warunkowe renderowanie</h1>
      <ToggleDetails />

      <h1>wiczenie 2  Wynik uytkownika</h1>
      <input
        type="text"
        value={userScore}
        onChange={handleChange}
        placeholder="Wpisz swj wynik"
      />
      {userScore !== '' && <ScoreDisplay score={parseInt(userScore, 10)} />}

      <h1>wiczenie 3  Lista zada</h1>
      <TaskList />

      <h1>wiczenia 4 - Lista uytkownikw</h1>
      <UserList />

      <h1>wiczenia 5 - Licznik</h1>
      <TimerCounter />
    </div>
  );
}

export default App;
